// (Все, кроме главной) Хлебные крошки

.breadcrumbs {
	display: flex;
	align-items: center;
	&__link {
		text-decoration: none;
		color: $dark;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		&--static {
			color: #648179;
		}
	}
	&__icon {
		margin-left: 25px;
		margin-right: 25px;
	}
}